@import "ajax/functions";
@import "ajax/variables";
@import "ajax/mixins";
@import "ajax/cookie";

/* roboto-300 - latin */
@font-face {
    font-family: 'Roboto Light';
    font-style: normal;
    font-weight: 300;
    src: local('Roboto Light'), local('Roboto-Light'),
       url('../fonts/Roboto/roboto-300.woff2') format('woff2'),
       url('../fonts/Roboto/roboto-300.woff') format('woff');
    font-display: swap;
}
/* roboto-regular - latin */
@font-face {
    font-family: 'Roboto Regular';
    font-style: normal;
    font-weight: 400;
    src: local('Roboto'), local('Roboto-Regular'),
       url('../fonts/Roboto/roboto-regular.woff2') format('woff2'),
       url('../fonts/Roboto/roboto-regular.woff') format('woff');
    font-display: swap;
}
/* roboto-500 - latin */
@font-face {
    font-family: 'Roboto Medium';
    font-style: normal;
    font-weight: 500;
    src: local('Roboto Medium'), local('Roboto-Medium'),
       url('../fonts/Roboto/roboto-500.woff2') format('woff2'),
       url('../fonts/Roboto/roboto-500.woff') format('woff');
    font-display: swap;
}

@at-root {
    @-ms-viewport { width: device-width; }
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -ms-overflow-style: scrollbar;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -ms-overflow-style: scrollbar;
}
article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
    display: block;
}
body {
    background-color: $gray2;
    color: $brand1;
    display: flex;
    font-family: "Roboto Regular", sans-serif;
    font-size: 16px;
    font-weight: 400;
    flex-direction: column;
    line-height: $line-height;
    min-height: 101vh;
    margin: 0;
    text-align: left;
    overflow-x: hidden;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    @include media-breakpoint-up(lg) {
        font-size: 18px;
    }
}

p {
    margin-top: 0;
    margin-bottom: $paragraph-margin-bottom;
}

ol,
ul {
    margin-top: 0;
    margin-bottom: 0;
}

a {
    color: $link-color;
    text-decoration: $link-decoration;
    background-color: transparent;
    -webkit-text-decoration-skip: objects;
    @include transition($link-transition);
    @include hover {
        color: $link-hover-color;
        text-decoration: $link-decoration;
    }
}

img {
    display: inline-block;
    vertical-align: middle;
    border-style: none;
    max-width: 100%;
    &[data-src],
    &[data-srcset] {
        min-height: 1px;
    }
}

svg:not(:root) {
    overflow: hidden;
}
.svg-icon {
    display: inline-block;
    font-size: inherit;
    height: 1em;
    overflow: visible;
    vertical-align: -.125em;
    width: 1em;
}
input,
button,
select,
optgroup,
textarea {
    margin: 0;
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
}

button,
input {
    overflow: visible;
}

button,
select {
    text-transform: none;
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
    -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
    padding: 0;
    border-style: none;
}

input[type="radio"],
input[type="checkbox"] {
    box-sizing: border-box;
    padding: 0;
}

video {
    outline: 0;
}
h2,h3,h4 {
    line-height: 1.2;
}
h2 {
    font-family: 'Roboto Medium';
    font-size: 32px;
    font-weight: 500;
    margin: 0 0 32px;
    @include media-breakpoint-up(sm) {
        font-size: 40px;
    }
    @include media-breakpoint-up(lg) {
        font-size: 48px;
    }
}
h3 {
    font-family: 'Roboto Medium';
    font-size: 24px;
    font-weight: 500;
    margin: 0 0 32px;
    @include media-breakpoint-up(lg) {
        font-size: 32px;
    }
}
h4 {
    font-family: 'Roboto Medium';
    font-size: 24px;
    font-weight: 500;
    margin: 0 0 8px;
}
h1 {
    font-family: 'Roboto Regular';
    font-size: 24px;
    font-weight: 400;
    line-height: 0.9;
    margin: 32px 0;
    @include media-breakpoint-up(sm) {
        margin: 48px 0;
    }
    @include media-breakpoint-up(md) {
        font-size: 32px;
    }
    @include media-breakpoint-up(lg) {
        font-size: 62px;
        margin: 64px 0;
    }
}
.font-light {
    font-family: 'Roboto Light';
    font-weight: 300;
}
.text-white {
    color: #fff;
}
.btn {
    align-items: center;
    border: 0;
    border-radius: 24px;
    color: $brand1;
    display: inline-flex;
    height: 40px;
    font-family: 'Roboto Medium';
    font-weight: 500;
    font-size: 14px;
    min-width: 224px;
    padding: 0;
    justify-content: center;
    white-space: nowrap;
    user-select: none;
    transition: background .2s ease-in-out;
    width: fit-content;
    &:focus {
        outline: 0;
    }
    &.btn-primary {
        background-color: $green1;
        &:hover,
        &:focus {
            background: darken($green1,6%);
            color: $brand1;
        }
    }
    &.btn-border {
        border: 1px solid $green1;
        color: $green1;
        transition: background .2s ease-in-out, color .2s ease-in-out;
        &:hover,
        &:focus {
            background: $green1;
            color: $brand1;
        }
    }
}
.container {
    width: 100%;
    max-width: 1536px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 16px;
    padding-left: 16px;
}
.row {
    display: flex;
    flex-wrap: wrap;
    margin-right: -16px;
    margin-left: -16px;
    &.no-gutter {
        margin-right: 0;
        margin-left: 0;
    }
}
.text-center {
    text-align: center;
}
.bg-gray1 {
    background: $gray1;
}
.bg-brand1 {
    background: $brand1;
}
.bg-white {
    background: #fff;
}
.block-p96 {
    padding: 96px 0;
}
.block-p128 {
    padding: 96px 0;
    @include media-breakpoint-up(xl) {
        padding: 128px 0;
    }
}
.mb-64 {
    margin-bottom: 64px;
}
.mb-48 {
    margin-bottom: 48px;
}
.icons-list {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 48px 0 0;
    padding: 0;
    @include media-breakpoint-up(md) {
        margin: 64px 0 0;
    }
    li {
        align-items: center;
        display: flex;
        font-size: 16px;
    }
}
.icons-list__type1 {
    li {
        padding: 16px 0;
        &:not(:last-child) {
            margin-right: 64px;
        }
    }
    .icon {
        margin-right: 16px;
    }
}
.icons-list__type2 {
    align-items: flex-start;
    li {
        margin-bottom: 32px;
        flex: 0 0 100%;
        max-width: 100%;
        @include media-breakpoint-up(md) {
            flex-direction: column;
            flex: 0 0 224px;
            max-width: 224px;
            margin-bottom: 48px;
            justify-content: center;
            text-align: center;
            width: 224px;
        }
    }
    .icon {
        margin-right: 24px;
        @include media-breakpoint-up(md) {
            margin-bottom: 24px;
            margin-right: 0;
        }
    }
}
.icons-list__type3 {
    li {
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 48px;
        padding: 0 16px;
        @include media-breakpoint-up(md) {
            flex: 0 0 50%;
            max-width: 50%;
        }
    }
    .icon {
        margin-right: 24px;
    }
}
.round-icon {
    align-items: center;
    border-radius: 50%;
    display: flex;
    height: 88px;
    justify-content: center;
    overflow: hidden;
    width: 88px;
    flex: 0 0 88px;
    max-width: 88px;
}

.header {
    background: $brand1;
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1000;
    @include media-breakpoint-down(sm) {
        display: block !important;
    }
}
.header-container {
    align-items: center;
    display: flex;
    height: 60px;
    justify-content: space-between;
    padding: 0 0 0 16px;
    @media (min-width: 1200px) {
        padding: 0 0 0 48px;
    }
}
.logo {
    font: 0/0 a;
    color: transparent;
    text-shadow: none;
    background-color: transparent;
    border: 0;
    svg {
        fill: #fff;
        width: 76px;
    }
}
.header-btn {
    background: $green1;
    color: $brand1;;
    font-size: 14px;
    height: 60px;
    line-height: 60px;
    letter-spacing: .9px;
    margin-left: auto;
    text-transform: uppercase;
    padding: 0 16px;
    transition: background .2s ease-in-out;
    &:hover,
    &:focus {
        background: darken($green1,6%);
        color: #181818;
    }
    @media (min-width: 992px) {
        font-size: 18px;
        padding: 0 24px;
    }
}

.block1 {
    align-items: center;
    background: #000;
    padding-top: 60px;
    position: relative;
    width: 100%;
    &:after {
        background: rgba(#000,.1);
        content: '';
        height: 100%;
        left: 0;
        top: 0;
        position: absolute;
        width: 100%;
    }
    @include media-breakpoint-up(sm) {
        display: flex;
        min-height: 100vh;
        padding-top: 0;
    }
    @include media-breakpoint-up(xl) {
        height: 100vh;
        max-height: 1080px;
    }
    .block1-media {
        height: 324px;
        position: relative;
        width: 100%;
        @include media-breakpoint-up(sm) {
            height: 100%;
            position: absolute;
        }
    }
    video {
        height: 100%;
        left: 0;
        top: 0;
        object-fit: cover;
        position: absolute;
        width: 100%;
    }
    img {
        height: 100%;
        left: 0;
        top: 0;
        object-fit: cover;
        position: absolute;
        width: 100%;
    }
    .content {
        max-width: 752px;
        margin: 0 auto;
        padding: 48px 16px 96px;
        text-align: center;
        z-index: 1;
        @include media-breakpoint-up(sm) {
            padding: 100px 16px;
        }
        @include media-breakpoint-up(lg) {
            margin: 0;
            text-align: left;
        }
    }
    .logo-icon {
        width: 152px;
        @include media-breakpoint-up(sm) {
            width: 227px;
        }
        @include media-breakpoint-up(lg) {
            width: 350px;
        }
    }
    .text {
        font-size: 16px;
        max-width: 647px;
        margin: 0 auto;
        @include media-breakpoint-up(md) {
            font-size: 18px;
        }
        @include media-breakpoint-up(lg) {
            font-size: 20px;
            margin: 0;
        }
    }
    .btn-block {
        margin-top: 48px;
    }
}
.block2 {
    padding: 96px 0 0;
    @include media-breakpoint-up(xl) {
        padding: 96px 0 32px;
    }
    h2 {
        margin-bottom: 64px;
        @include media-breakpoint-up(lg) {
            margin-bottom: 72px;
        }
    }
}
.promo-card {
    flex: 0 0 100%;
    max-width: 100%;
    position: relative;
    overflow: hidden;
    @include media-breakpoint-up(xl) {
        margin-bottom: 64px;
    }
    @include media-breakpoint-up(xxl) {
        padding: 480px 48px 80px;
    }
    &.open {
        &:before {
            background: rgba($brand1, .8);
            content: '';
            height: 100%;
            left: 0;
            top: 0;
            position: absolute;
            width: 100%;
            z-index: 10;
        }
    }
    .content {
        position: relative;
        z-index: 2;
    }
    .media-block {
        display: flex;
        align-items: center;
        height: 320px;
        padding: 24px;
        justify-content: center;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        @include media-breakpoint-up(lg) {
            height: 350px;
        }
        @include media-breakpoint-up(xl) {
            height: 400px;
        }
        img, video {
            max-width: 95%;
            @include media-breakpoint-up(sm) {
                max-width: 450px;
            }
        }
    }
    h3 {
        font-family: 'Roboto Regular';
        font-weight: 400;
    }
}
.card1 {
    display: flex;
    flex: 0 0 100%;
    max-width: 100%;
    flex-direction: column;
    @include media-breakpoint-between(md,lg) {
        flex-direction: row;
    }
    @include media-breakpoint-up(xl) {
        padding: 400px 24px 64px;
        flex: 0 0 33.3333%;
        max-width: 33.3333%;
        flex-direction: column;
    }
    @include media-breakpoint-up(xxl) {
        padding: 400px 48px 64px;
    }
    &:after {
        top: 200px;
        content: '';
        height: 264px;
        left: 0;
        position: absolute;
        width: 100%;
        z-index: 1;
        @include media-breakpoint-between(md,lg) {
            display: none;
        }
        @include media-breakpoint-up(xl) {
            top: 28%;
            height: 570px;
        }
    }
    .preview-block {
        height: 320px;
        position: relative;
        width: 100%;
        @include media-breakpoint-up(md) {
            height: auto;
            width: auto;
        }
        @include media-breakpoint-between(md,lg) {
            flex: 0 0 328px;
            max-width: 328px;
        }
        @include media-breakpoint-up(xl) {
            height: 400px;
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
        }
        .case-video, img {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }
    .content {
        padding: 0 16px 64px;
        @include media-breakpoint-up(md) {
            padding: 48px 24px;
        }
        @include media-breakpoint-up(xl) {
            padding: 0;
        }
    }
    .text {
        @include media-breakpoint-up(md) {
            min-height: 120px;
        }
    }
    .video-btn {
        left: 16px;
        top: -72px;
        position: absolute;
        @include media-breakpoint-up(md) {
            left: 0;
        }
        @include media-breakpoint-up(xl) {
            left: 0;
        }
    }
}
.card2 {
    padding: 320px 0 64px;
    @include media-breakpoint-up(lg) {
        padding: 350px 0 64px;
        flex: 0 0 50%;
        max-width: 50%;
    }
    @include media-breakpoint-up(xl) {
        padding: 400px 0 64px;
    }
    .content {
        padding: 0 16px;
        @include media-breakpoint-up(md) {
            padding: 0 24px;
        }
        @include media-breakpoint-up(xxl) {
            padding: 0 48px;
        }
    }
    &:after {
        bottom: 0;
        top: auto;
        content: '';
        height: 324px;
        left: 0;
        position: absolute;
        width: 100%;
        z-index: 1;
        @include media-breakpoint-up(md) {
            top: 11%;
            height: 570px;
        }
        @include media-breakpoint-up(lg) {
            height: 400px;
            top: 35%;
        }
        @include media-breakpoint-up(xxl) {
            height: 570px;
            top: 28%;
        }
    }
    .video-btn {
        left: 16px;
        top: -72px;
        position: absolute;
        @include media-breakpoint-up(md) {
            left: 24px;
        }
        @include media-breakpoint-up(xl) {
            left: 48px;
        }
    }
}
.cases-type1 {
    @include media-breakpoint-up(md) {
        margin-left: 0;
        margin-right: 0;
    }
}
.cases-type2 {
    @include media-breakpoint-up(md) {
        margin-top: 64px;
    }
    @include media-breakpoint-up(xl) {
        margin-left: 0;
        margin-right: 0;
        margin-top: 0;
    }
}
.case-img,
.case-video {
    left: 0;
    top: 0;
    position: absolute;
    width: 100%;
}
.case1 {
    background: #050c15;
    &:after {
        background-image: linear-gradient(to top, #050c15, #070c0f 65%, rgba(6, 22, 35, 0));
    }
}
.case2 {
    background: #464945;
    &:after {
        background-image: linear-gradient(to top, #464945, #464945 65%, rgba(70, 73, 69, 0));
    }
}
.case3 {
    background: #225760;
    &:after {
        background-image: linear-gradient(to top, #225760, #225a60 65%, rgba(34, 88, 97, 0));
    }
}
.case4 {
    background: #232429;
    &:after {
        background-image: linear-gradient(to bottom, rgba(59, 59, 64, 0), rgba(47, 48, 53, 0.7) 23%, rgba(34, 34, 40, 0.99) 52%, #232429);
    }
}
.case5 {
    background: #ededed;
    &:after {
        background-image: linear-gradient(to top, #ededed, #ededed 40%, rgba(#ededed, 0));
    }
}
.case4, 
.case5 {
    .case-img {
        height: 94%;
        object-fit: cover;
        object-position: center top;
        @include media-breakpoint-up(sm) {
            height: auto;
            object-fit: none;
        }
    }
}
.case6 {
    background: #dbd3bf;
}
.case7 {
    background: #24150a;
}
.usecase-video-block {
    display: none;
    left: 0;
    top: 0;
    position: absolute;
    padding: 20px;
    width: 100%;
    z-index: 11;
    .video {
        width: 100%;
    }
    .video-container {
        max-width: 480px;
        margin: 0 auto;
    }
    .video-close {
        background: $brand1;
        border: 0;
        color: #fff;
        cursor: pointer;
        height: 48px;
        top: 0;
        right: -0;
        padding: 0;
        position: absolute;
        outline: 0;
        width: 48px;
        transition: background .15s ease-in-out;
        z-index: 1;
        &:hover {
            background: rgba($brand1,.9);
        }
    }
}
.video-btn {
    background: transparent;
    border: 1px solid #fff;
    border-radius: 50%;
    cursor: pointer;
    padding: 0;
    height: 48px;
    position: relative;
    outline: 0;
    width: 48px;
    animation: blinker 1s cubic-bezier(.5, 0, 1, 1) infinite alternate;  
    &:hover {
        opacity: .8;
    }
    &:before {
        border-bottom: 6px solid transparent;
        border-top: 6px solid transparent;
        border-left: 12px solid #fff;
        content: '';
        left: 50%;
        top: 50%;
        margin-left: 1.5px;
        position: absolute;
        transform: translate(-50%, -50%);
    }
}
@keyframes blinker {  
    from { opacity: .4; }
    to { opacity: .8; }
}
.block3 {
    .block-title {
        max-width: 880px;
        margin: 0 auto;
        text-align: center;
        @include media-breakpoint-up(lg) {
            margin: 0;
            text-align: left;
        }
    }
    h2 {
        font-family: 'Roboto Light';
        font-weight: 300;
        margin-bottom: 64px;
        @include media-breakpoint-up(lg) {
            font-family: 'Roboto Regular';
            font-weight: 400;
            margin-bottom: 96px;
        }
    }
}
.block4 {
    background: $gray7;
    padding: 96px 0 48px;
    .text {
        margin: 0 auto;
        max-width: 992px;
    }
    .block-img {
        padding: 64px 0;
        @include media-breakpoint-up(lg) {
            padding: 96px 0;
        }
    }
}
.awards-list {
    align-items: flex-end;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
    width: 100%;
    li {
        color: $gray5;
        font-size: 12px;
        text-align: center;
        margin: 0 16px 48px;
        width: 100%;
        @media (min-width: 601px) {
            width: 234px;
        }
        .text {
            margin-top: 24px;
        }
    }
    .icon2 {
        opacity: .5;
    }
}

@import "tiny-slider/tiny-slider";

.block5 {
    h2 {
        margin-bottom: 64px;
    }
}
.product-slider-row {
    justify-content: center;
    position: relative;
}
.product-slider {
    display: flex;
    flex-direction: column;
    flex: 0 0 100%;
    max-width: 100%;
    padding: 0 16px;
    @include media-breakpoint-up(xl) {
        flex: 0 0 85%;
        max-width: 85%;
    }
    @include media-breakpoint-up(xxl) {
        flex: 0 0 75%;
        max-width: 75%;
    }
}
.item-container {
    background: $brand1;
    border-radius: 16px;
    height: 100%;
    overflow: hidden;
    padding: 250px 24px 32px;
    position: relative;
    @include media-breakpoint-up(xl) {
        padding: 336px 24px 32px;
    }
    &:after {
        background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.7));
        content: '';
        height: 100%;
        left: 0;
        top: 0;
        position: absolute;
        width: 100%;
    }
    img {
        height: 100%;
        left: 0;
        top: 0;
        position: absolute;
        object-fit: cover;
        width: 100%;
    }
    .content {
        position: relative;
        z-index: 1;
    }
    .text {
        font-size: 16px;
    }
    .price {
        color: $green1;
        font-family: 'Roboto Medium';
        font-weight: 500;
        margin-bottom: 8px;
    }
}
.slider-controls {
    flex: 0 0 100%;
    max-width: 100%;
    outline: 0;
}
.slider-btn {
    align-items: center;
    background: $brand1;
    border: 0;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    height: 40px;
    justify-content: center;
    position: absolute;
    top: 100%;
    outline: 0;
    width: 40px;
