        max-width: 384px;
    }
}
.form-control {
    background-color: transparent;
    border: 0;
    border-bottom: 1px solid #fff;
    border-radius: 0;
    color: #fff;
    display: block;
    font-size: 18px;
    padding: 0;
    width: 100%;
    transition: border .2s ease;
    &:focus {
        border-color: $green1;
        outline: 0;
    }
}
.plain-label {
    color: $gray4;
    display: block;
    font-size: 16px;
    margin-bottom: 16px;
}
.select-block {
    display: block;
    position: relative;
    .style-select {
        background: transparent;
        border: 1px solid #fff;
        border-radius: 20px;
        color: #fff;
        font-family: 'Roboto Medium';
        font-weight: 500;
        height: 40px;
        padding: 0 60px 0 20px;
        appearance:none;
        width: 100%;
        &:focus {
            outline: 0;
        }
        &.error {
            & + .chevron {
                border-color: $error !important;
            }
        }
    }
    option {
        color: $brand1;
    }
    .chevron {
        align-items: center;
        border: 1px solid #fff;
        border-top-right-radius: 20px;
        border-bottom-right-radius: 20px;
        display: flex;
        justify-content: center;
        right: 0;
        top: 0;
        position: absolute;
        pointer-events: none;
        height: 40px;
        width: 40px;
        svg {
            margin-left: -2px;
        }
    }
}
.styled-checkbox {
    font-family: 'Roboto Regular';
    font-weight: 500;
    color: $gray4;
    font-size: 14px;
    display: inline-block;
    padding-left: 36px;
    cursor: pointer;
    user-select: none;
    position: relative;
    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        &:checked ~ .checkmark {
            &:before {
                display: block;
            }
        }
    }
    .checkmark {
        position: absolute;
        top: 0;
        left: 0;
        height: 24px;
        width: 24px;
        background-color: transparent;
        border: 2px solid $green1;
        &:before {
            content: '';
            top: 2px;
            left: 2px;
            width: 7px;
            height: 14px;
            display: none;
            border-top: 0 solid transparent;
            border-left: 0 solid transparent;
            border-right: 2px solid $green1;
            border-bottom: 2px solid $green1;
            position: absolute;
            transform: rotateZ(37deg);
            transform-origin: 100% 100%;
        }
    }
}
.privacy-checkbox {
    margin-bottom: 24px;
}
.styled-radio {
    color: #fff;
    display: inline-block;
    font-size: 16px;
    position: relative;
    margin-bottom: 24px;
    padding-left: 36px;
    cursor: pointer;
    user-select: none;
    &:not(:last-child) {
        margin-right: 24px;
    }
    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        &:checked ~ .checkmark {
            background-color: $green1;
            &:after {
                display: block;
            }
        }
    }
    .checkmark {
        position: absolute;
        top: 0;
        left: 0;
        height: 24px;
        width: 24px;
        background-color: transparent;
        border: 2px solid $green1;
        border-radius: 50%;
        &:after {
            content: "";
            position: absolute;
            display: none;
            top: 50%;
        	left: 50%;
        	width: 10px;
        	height: 10px;
        	border-radius: 50%;
        	background: $brand4;
            transform: translate(-50%, -50%);
        }
    }
}

.input-label {
    color:#fff; 
    font-size: 18px;
    font-family: 'Roboto Regular';
    font-weight: 400;
    position: absolute;
    pointer-events: none;
    left: 0;
    top: 0;
    transition: .2s ease all; 
} 
.form-control.not-empty ~ .input-label, 
.form-control:focus ~ .input-label, 
.form-control:valid ~ .input-label {
    top: -24px;
    font-size: 14px;
}
.form-control.not-empty ~ .input-label,
.form-control:valid ~ .input-label {
    color: $gray4;
}
.form-control:focus ~ .input-label {
    color: $green1;
}
.submit-btn {
    width: 100%;
    @include media-breakpoint-up(sm) {
        max-width: 296px;
    }
    &:not(:disabled) {
        cursor: pointer;
    }
    &:disabled {
        background: transparent;
        border: 1px solid $gray4;
        color: $gray4;
        &:hover,
        &:focus {
            background: transparent;
            border: 1px solid $gray4;
            color: $gray4;
        }
    }
}
.success-msg {
    position: fixed;
    top: -100px;
    left: 0;
    width: 100%;
    z-index: 1001;
    transition: top .2s ease-in-out;
    .success-content {
        background: $green1;
        font-size: 20px;
        padding: 16px 24px;
        max-width: 1000px;
        margin: 0 auto;
