.ajax-spinner {
    .inner,
    .outer {
        transform-origin: 50% 50%;
    }
    .inner {
        animation: spin .6s infinite linear;
    }
    .outer {
        animation: spin 1.5s infinite linear;
        animation-direction: reverse;
    }
}
@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(359deg);
    }
