        padding: 0 16px;
    }
    .owl-stage-outer {
        padding-bottom: 128px;
    }
    h3 {
        font-size: 24px;
        margin: 0 0 32px;
    }
    .text {
        margin-top: 32px;
    }
    .owl-nav {
        position: absolute;
        z-index: 2;
        left: 0;
        padding-left: 16px;
        bottom: 64px;
        .owl-prev,
        .owl-next {
            background: $gray7;
            color: $brand1;
            transition: background .1s ease-in-out;
            &:hover {
                background: darken($gray7, 20%);
            }
        }
    }
    .owl-dots {
        align-items: center;
        display: flex;
        height: 40px;
        position: absolute;
        bottom: 64px;
        padding-left: 130px;
        z-index: 1;
        button {
            border-radius: 50%;
            height: 8px;
            width: 8px;
            background: $gray6;
            &.active {
                background: $gray7;
            }
            &:focus,
            &.focus {
                outline: 0;
            }
            &:not(:last-child) {
                margin-right: 8px;
            }
        }
    }
    .owl-nav {
        .owl-prev,
        .owl-next {
            cursor: pointer;
            height: 40px;
            width: 40px;
            &:focus,
            &.focus {
                outline: 0;
            }
            svg {
                display: inline-block;
                position: relative;
                vertical-align: middle;
            }
        }
        .owl-next {
            border-bottom-right-radius: 20px;
            border-top-right-radius: 20px;
        }
        .owl-prev {
            margin-right: 2px;
            border-bottom-left-radius: 20px;
            border-top-left-radius: 20px;
            svg {
                left: -1px;
            }
        }
    }
}
.scenario-block2__media {
    flex: 0 0 100%;
    max-width: 100%;
    padding: 0 16px;
    @include media-breakpoint-up(md) {
        flex: 0 0 66.66667%;
        max-width: 66.66667%;
    }
    @include media-breakpoint-up(lg) {
        flex: 0 0 50%;
        max-width: 50%;
    }
}
.scenario-block2__list {
    font-family: 'Roboto Light';
    font-weight: 300;
    font-size: 18px;
    padding: 96px 16px;
    flex: 0 0 100%;
    max-width: 100%;
    @include media-breakpoint-up(md) {
        flex: 0 0 33.3333%;
        max-width: 33.3333%;
    }
    @include media-breakpoint-up(lg) {
        font-size: 24px;
        padding: 128px 16px;
        flex: 0 0 50%;
        max-width: 50%;
    }
    @include media-breakpoint-up(xl) {
        padding: 160px 16px;
        font-size: 32px;
    }
    ul {
        @include list-unstyled;
    }
    li {
        &:not(:last-child) {
            margin-bottom: 24px;
            @include media-breakpoint-up(lg) {
                margin-bottom: 48px;
            }
        }
    }
    a {
        color: $brand6;
        display: inline-block;
        @include hover {
            color: #fff;
        }
        &.active {
            @include plain-hover-focus {
                color: #fff;
            }
            .carret {
                opacity: 1;
            }
        }
    }
    .carret {
        bottom: 2px;
        height: 14px;
        position: relative;
        width: 9px;
        opacity: 0;
        transition: opacity .2s ease-in-out;
        &:before {
            content: '';
            border-top: 4px solid transparent;
            border-bottom: 4px solid transparent;
            border-left: 6px solid $green1;
            left: 0;
            top: 50%;
            position: absolute;
            @include media-breakpoint-up(lg) {
                border-top-width: 7px;
                border-bottom-width: 7px;
                border-left-width: 9px;
                transform: translate(0,-2px);
            }
        }
    }
}
.scenario-tab__panel {
    color: #fff;
    padding: 48px 0;
    @include media-breakpoint-up(lg) {
        padding: 64px 0;
    }
    @include media-breakpoint-up(xl) {
        font-size: 18px;
        padding: 96px 0;
    }
    &:not(.active) {
        display: none;
    }
    .text {
        font-family: 'Roboto Light';
        font-weight: 300;
        max-width: 615px;
    }
    .video-block {
        max-width: 608px;
    }
    .scenarios-media {
        position: relative;
    }
} 
.video-block {
    position: relative;
    video {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
    }
}
.replay-block {
    display: none;
    position: absolute;
    top: 0;
    right: 16px;
    z-index: 1;
    @include media-breakpoint-up(xl) {
        right: 48px;
    }
}
.replay-btn {
    background: transparent;
    border-radius: 0;
    border: 0;
    color: #fff;
    height: 32px;
    font-size: 14px;
    padding: 0;
    display: flex;
    align-items: center;
    width: fit-content;
    transition: color .15s ease-in-out;
    &:hover {
        color: $green1;
    }
    svg {
        margin-right: 8px;
    }
}
.scenario-steps {
    @include list-unstyled;
    display: flex;
    flex-wrap: wrap;
    margin: 32px 0 16px;
    @include media-breakpoint-up(xl) {
        margin: 40px 0 24px;
    }
    li {
        margin-bottom: 8px;
        position: relative;
        &:not(:last-child) {
            margin-right: 24px;
            @include media-breakpoint-up(xl) {
                margin-right: 32px;
            }
            &:after {
                border-top: 5px solid transparent;
                border-bottom: 5px solid transparent;
                border-left: 6px solid $brand6;
                content: '';
                left: 100%;
                top: 50%;
                transform: translate(8px, -50%);
                position: absolute;
            }
        }
    }
    .scenario-steps__card {
        align-items: center;
        background: $brand4;
        border-radius: 16px;
        color: $gray4;
        display: flex;
        flex-direction: column;
        font-size: 14px;
        padding: 12px;
        @include media-breakpoint-up(lg) {
            flex-direction: row;
            padding: 8px 16px 8px 8px;
        }
        @include media-breakpoint-up(xl) {
            padding: 8px 16px 8px 12px;
        }
    }
    .scenario-icon {
        flex: 0 0 40px;
        max-width: 40px;
        margin-bottom: 8px;
        @include media-breakpoint-up(lg) {
            margin-bottom: 0;
            margin-right: 8px;
        }
        @include media-breakpoint-up(xl) {
            flex: 0 0 56px;
            max-width: 56px;
        }
    }
}
.block-fees {
    background: #fff;
    padding: 64px 0;
    @include media-breakpoint-up(md) {
        padding: 96px 0;
    }
    .fees-container {
        align-items: center;
        display: flex;
        flex-wrap: wrap;
        flex: 0 0 100%;
        max-width: 100%;
        padding: 0 16px;
        @include media-breakpoint-up(md) {
            flex-wrap: nowrap;
        }
        @include media-breakpoint-up(xl) {
            flex: 0 0 83.33333%;
            max-width: 83.33333%;
            margin-left: 8.33333%;
        }
    }
    .fees-content {
        flex: 0 0 1;
        max-width: 694px;
        @include media-breakpoint-down(sm) {
            flex: 0 0 100%;
            max-width: 100%;
            text-align: center;
        }
    }
    .fees-icon {
        @include media-breakpoint-up(md) {
            padding-right: 132px;
        }
        @include media-breakpoint-up(xl) {
            padding-right: 200px;
        }
        @include media-breakpoint-down(sm) {
            flex: 0 0 100%;
            max-width: 100%;
            margin-bottom: 64px;
            text-align: center;
        }
    }
    img {
        width: 160px;
        min-width: 160px;
        @include media-breakpoint-up(md) {
            width: 200px;
            min-width: 200px;
        }
        @include media-breakpoint-up(xl) {
            width: 228px;
            min-width: 228px;
        }
    }
}

@import "sal/sal";
@import "owl-carousel/animate";
@import "owl-carousel/core";